import React from 'react'
import {makeStyles, Typography} from '@material-ui/core'
const useStyles = makeStyles({
  header: {
    borderBottom: '4px solid #ccc',
    padding: '20px 0',
    marginBottom: 32
  },
  headTitle: {
    fontSize: 40,
    fontWeight: 'bold'
  },
  subText: {
    fontSize: 20,
    marginLeft: 24
  }
})
export default function Header() {
  const classes = useStyles();
  return (
    <div className={classes.header}>
      <Typography variant="body1" component="h1" className={classes.headTitle}>S-antiCovi</Typography>
      <Typography variant="body1" component="p" className={classes.subText}>Thông tin tình hình dịch Covid trong và ngoài nước</Typography>
    </div>
  )
}
